import React from 'react';
import PropTypes from 'prop-types';
import SearchBoxImpl from './SearchBoxImpl';

const DSSearchBox = ({
  containerProps = {},
  innerRef = undefined,
  className = '',
  disabled = false,
  disableButton = false,
  searchIcon = 'search',
  style = {},
  property = 'all',
  placeholder = 'Search all', // todo: i18nize this
  searchOnEnter = true,
  clearOnSearch = false,
  onKeyUp = () => null,
  onSearch = () => null,
  onChange = () => null,
  onBlur = () => null,
  readOnly = false,
  value = undefined,
  showIcon = true,
}) => (
  <SearchBoxImpl
    className={className}
    clearOnSearch={clearOnSearch}
    containerProps={containerProps}
    disabled={disabled}
    disableButton={disableButton}
    innerRef={innerRef}
    onKeyUp={onKeyUp}
    onSearch={onSearch}
    onChange={onChange}
    onBlur={onBlur}
    placeholder={placeholder}
    property={property}
    readOnly={readOnly}
    searchIcon={searchIcon}
    searchOnEnter={searchOnEnter}
    showIcon={showIcon}
    style={style}
    value={value}
  />
);

DSSearchBox.propTypes = {
  containerProps: PropTypes.shape({}),
  className: PropTypes.string,
  innerRef: PropTypes.any,
  /**
   * Icon to use in the search button
   */
  searchIcon: PropTypes.string,
  /**
   * Set style to the search box
   */
  style: PropTypes.objectOf(PropTypes.string),
  property: PropTypes.string,
  /**
   * Placeholder for the search box
   */
  placeholder: PropTypes.string,
  /**
   * Whether to trigger the search once the enter is pressed
   */
  searchOnEnter: PropTypes.bool,
  /**
   * Whether to clear the input on search
   */
  clearOnSearch: PropTypes.bool,
  /**
   * Allows a function that is triggered once in the search box a keyboard key is released
   */
  onKeyUp: PropTypes.func,
  /**
   * Allows a function that is triggered once the search is clicked
   */
  onSearch: PropTypes.func,
  /**
   * Allows a function that is triggered on change
   */
  onChange: PropTypes.func,
  /**
   * Function that is triggered on blur
   */
  onBlur: PropTypes.func,
  /**
   * Selected default active value
   */
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.object,
  ]),
  /**
   * Whether to show an icon or not
   */
  showIcon: PropTypes.bool,
  /**
   * Disables the search input and button
   */
  disabled: PropTypes.bool,
  /**
   * Disables the search button
   */
  disableButton: PropTypes.bool,
  readOnly: PropTypes.bool,
};

export default DSSearchBox;
